.side-menu, .side-menu ul {
  list-style: none;
  padding: 0;
  margin-top: 0;
  display: inline-block;
}

.side-menu .md-button.active, 
.side-menu .md-button.active:hover, 
.side-menu .md-button:hover {
  color: #fff; /* deep-orange 500 */
  background: #53ab58;
}

.side-menu .menu-toggle-list a.md-button {
  display: block;
  padding: 10px 30px 10px 50px;
  text-transform: none;
  text-rendering: optimizeLegibility;
  // min-height: 5.5rem;
}

.side-menu .menu-toggle-list .md-button {
  min-height: auto;
  line-height: 0;
  display: block;
  padding: 10px 30px;
  text-transform: none;
}

.md-button-toggle .md-toggle-icon.toggled {
  transform: rotateX(180deg);
  -webkit-transform: rotateX(180deg);
}

.md-button-toggle.toggled {
  background-color: rgba(0, 0, 0, 0.02);
}

.side-menu .md-button-toggle .md-toggle-icon {
  margin: auto 0 auto auto;
  width: 15px;
  float: right;
  transition: transform .3s ease-in-out;
  -webkit-transition: -webkit-transform .3s ease-in-out;
}

.menu-toggle-list.ng-hide {
  max-height: 0;
}

.side-menu .md-button {
  border-radius: 0;
  color: #fff;
  cursor: pointer;
  margin: 0;
  overflow: hidden;
  padding: 10px 30px;
  text-align: left;
  text-decoration: none;
  white-space: normal;
  width: 100%;
  font-weight: 500;
  font-size: 20px;
}

.menu-toggle-list {
  max-height: 1300px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  border-right: 4px solid #53ab58;
  -webkit-transition: 0.75s cubic-bezier(0.35, 0, 0.25, 1);
  -webkit-transition-property: max-height;
  -moz-transition: 0.75s cubic-bezier(0.35, 0, 0.25, 1);
  -moz-transition-property: max-height;
  transition: 0.75s cubic-bezier(0.35, 0, 0.25, 1);
  transition-property: max-height;
}

.side-menu menu-toggle span, .side-menu menu-link span{
  font-family: 'Muli', sans-serif, arial, helvetica;
}

.md-panel.md-tooltip {
  background: $grey-dark;
  font-size: 11px;
  color: rgba(255,255,255,1);
  font-family: 'Muli', sans-serif, arial, helvetica;
  padding: 10px;
  height: auto;
  border-radius: 5px;
  background: $grey-dark;
  overflow: visible;
  -webkit-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.2);
  -moz-box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.2);
  box-shadow: 0px 3px 3px 0px rgba(0,0,0,0.2);
}

md-tooltip[md-direction="right"].md-tooltip:after {
  position: absolute;
  content: " ";
  top: 50%;
  left: 0%;
  margin-left: -7px;
  margin-top: -11px;
  width: 0;
  height: 0;
  border-top: 11px solid transparent;
  border-bottom: 11px solid transparent;
  border-right: 11px solid $grey-dark;
}

@media (min-width: 960px) {
  .md-tooltip {
    height: auto;
  }

}

