.notification-radio  {
  display: block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 15px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


/* Hide the browser's default radio button */
.notification-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: -1px;
  left: 0;
  height: 15px;
  width: 15px;
  background-color: $white;
  border-radius: 50%;
  &.green { border: 2px solid $green; }
  &.amber { border: 2px solid $amber; }
  &.red { border: 2px solid $red; }
  &.blue { border: 2px solid $blue; }
  &.black { border: 2px solid $black; }  
}

/* On mouse-over, add a grey background color */
.notification-radio:hover input ~ .checkmark {
  background-color: $white
}

/* When the radio button is checked, add a blue background */
.notification-radio input:checked ~ .checkmark {
  background-color: $white
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.notification-radio input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.notification-radio .checkmark {

  &.green:after {
    top: 2px;
    left: 2px;
    width: 7px;
    height: 7px;
    border-radius: 50%;    
    background: $green;
  }
  &.amber:after {
    top: 2px;
    left: 2px;
    width: 7px;
    height: 7px;
    border-radius: 50%;    
    background: $amber;
  }
  &.red:after {
    top: 2px;
    left: 2px;
    width: 7px;
    height: 7px;
    border-radius: 50%;    
    background: $red;
  }
  &.blue:after {
    top: 2px;
    left: 2px;
    width: 7px;
    height: 7px;
    border-radius: 50%;    
    background: $blue;
  }
  &.black:after {
    top: 2px;
    left: 2px;
    width: 7px;
    height: 7px;
    border-radius: 50%;    
    background: $black;
  } 
}

.checkbox-text {
  line-height: 4rem;
}


.icon-field{
  position: relative;
  .icon {
    position: absolute;
    right: 1.5rem;
    top: 50%;
    margin-top: -7px;
    font-size: 1.5rem;
    color: $grey-dark;
  }
}

.checkbox-round {
    width: 1.3em;
    height: 1.3em;
    background-color: $grey;
    border-radius: 50%;
    vertical-align: middle;
    border: 1px solid grey;
    -webkit-appearance: none;
    outline: none;
    cursor: pointer;
}

.checkbox-round:checked {
    background-color: $green;
}